/* post types list */
import isEmpty from 'lodash/isEmpty';
import filter from 'lodash/filter';
import includes from 'lodash/includes';

/* WP dependencies */
import ServerSideRender from '@wordpress/server-side-render';
import {
	RichText,
	useBlockProps
} from '@wordpress/block-editor';
import { SelectControl } from '@wordpress/components';
import {
	useSelect,
	withSelect
} from '@wordpress/data';
import { useState } from '@wordpress/element';
import { applyFilters } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';
import { PREFIX } from '../../utils/config';
import InputLabel from '../sitemaps/components/InputLabel';
import './editor.scss';
import { Fragment } from 'react';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @author WebDevStudios
 * @since 2.0.0
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * Editing interface in the admin
 *
 * @param {Object} [props]    Properties passed from the editor.
 * @param          attributes
 * @param          isSelected
 * @param          contentTypes
 *
 * @return {WPElement} Element to render.
 */
export default function Edit( props ) {
	const {
		attributes: { title, contentType },
		setAttributes,
		className,
		isSelected,
	} = props;

	let postTypes = [];
	postTypes = useSelect( ( select ) =>
		select( 'core' ).getPostTypes( {
			per_page: -1
		} )
	);

	postTypes = postTypes.filter(
		( postType ) =>
			postType.viewable === true && postType.slug !== 'attachment'
	);

	return (
		<div className={ className }>
			<RichText
				tagName="h3"
				role="button"
				tabIndex="0"
				className={ `${ className }__title` }
				onChange={ ( value ) => setAttributes( { title: value } ) }
				value={ title ? title : '' }
				placeholder={ __( 'Section Title', 'wdsblocks' ) }
				aria-expanded="false"
				allowedFormats={ [ 'core/bold', 'core/italic' ] }
			/>
			<div
				className={ `${ className }__content` }
				aria-hidden="true"
				tabIndex="-1"
			>
				<div className={ `${ className }__content--inner` }>
					{ ! isSelected && (
						<ServerSideRender
							block="wdsblocks/sitemap-item"
							attributes={ props.attributes }
						/>
					) }
					{ isSelected && (
						<Fragment>
							<SelectControl
								className="lep-sitemap__content_type"
								label={ __( 'Post Type', 'wdsblocks' ) }
								value={ props.attributes.contentType }
								onChange={ ( contentType ) =>
									setAttributes( {
										contentType,
									} )
								}
								options={ [
									{
										value: '',
										label: __(
											'Select a post type',
											'wdsblocks'
										),
									},
								].concat(
									postTypes.map( ( postType ) => {
										return {
											label: postType.name,
											value: postType.slug,
										};
									} )
								)}
							/>
						</Fragment>
					) }
				</div>
			</div>
		</div>
	);
}
